










import { Component, Vue } from "vue-property-decorator";
import useSidebar from "@/components/Sidebar/useSidebar";
@Component({
  setup() {
    const { isOpen, open, close } = useSidebar();
    const toggle = () => {
      if (isOpen.value) {
        close();
      } else {
        open();
      }
    };
    return {
      isOpen,
      toggle,
    };
  },
})
export default class HeaderBurger extends Vue {}
