






























import useMask from "@/compositions/useMask";
import { onMounted, ref, toRefs } from "@vue/composition-api";
import { Component, Prop, Vue } from "vue-property-decorator";
interface IProps {
  [key: string]: any;
  mask: string;
  maskPlaceholder: string;
}
@Component({
  setup(props: IProps) {
    const { mask, maskPlaceholder } = toRefs<IProps>(props);
    const el = ref(null);
    onMounted(() => {
      if (mask.value) {
        useMask(el.value, mask.value, { placeholder: maskPlaceholder.value });
      }
    });
    return { el };
  },
})
export default class AppInput extends Vue {
  @Prop(Object) inputAttrs: any;
  @Prop(String) label: string;
  @Prop({ type: Array, default: () => [] }) errors: string[];
  @Prop(String) value: string;
  @Prop(String) type: string;
  @Prop(String) sufix: string;
  @Prop(String) prefix: string;
  @Prop({ type: Boolean, default: false })
  inline: boolean;
  @Prop(String) width: string;
  @Prop({ type: Boolean, default: true }) showErrors: boolean;
  @Prop(String) mask: string;
  @Prop({ type: String, default: "_" }) maskPlaceholder: string;
  @Prop({ type: Boolean, default: false }) disabled: boolean;
  get _inputAttrs() {
    const attrs = Object.assign(
      {},
      { placeholder: this.label, value: this.value },
      this.inputAttrs
    );
    return attrs;
  }
  get _inputListeners() {
    const listeners = Object.assign({}, this.$listeners, {
      input: this.onInput,
      change: this.onInput,
      paste: this.onInput,
    });
    return listeners;
  }
  get inputStyles() {
    return {
      width: this.width,
    };
  }
  get hasErrors() {
    return this.errors.length > 0;
  }
  onInput(e: any) {
    const value = e.target.value;
    this.emitData(value);
  }
  emitData(data: any) {
    this.$emit("input", data);
  }
}
